// flex-template
@mixin flexbox ($direction, $justify, $align, $gap: null) {
  display: flex;
  flex-direction: $direction;
  justify-content: $justify;
  align-items: $align;
  gap: $gap;
}

// button position
@mixin media {
  @media screen and (min-width: 420px) {
    right: 0rem;
  }
}
